    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                startTime: '',
                endTime: '',
                value: '',  // 邮箱地址
            }
        },
        methods:{
//            导出列表
            list(){
                if(this.startTime == '' && this.endTime == ''){
                    this.$vux.toast.text('开始或结束时间不能为空', 'middle');
                }else if(this.value == ''){
                    this.$vux.toast.text('邮箱不能为空', 'middle');
                }else{
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.getExcelSend,
                        type: "post",
                        data: {
                            time: this.startTime,
                            times: this.endTime,
                            email: this.value
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.$vux.toast.text(allResponse.msg, 'middle');
                            setTimeout(()=>{
                                this.$router.push('/check/bossturn');
                            },900)
                        }
                    });
                }
            }
        }
    }



